@import 'src/assets/scss/variables';

.number-picker {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  &__input {
    width: 10rem;
    border: none;
    outline: none;
    box-shadow: none;
    background: transparent;
    color: $white;
    font-size: 2.66rem;
    font-family: 'Bangers';
    text-align: center;
  }

  &__input-wrapper {
    display: flex;
    flex-direction: row;
    outline: 2px solid $greyNormal;
    box-sizing: border-box;
  }

  &__input-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 5rem;
    height: 5rem;
    background: $greyNormal;
    color: $greyDefault;
    font-style: italic;
    font-size: 2.66rem;
    line-height: 3.19rem;
    cursor: pointer;

    &:hover {
      color: $white;
    }

    &:active {
      color: $pinkDefault;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    outline: 2px solid $greyNormal;
    font-family: 'Bangers';
    font-style: normal;
  }
}

@media screen and (max-width: 480px) {
  .number-picker {
    width: 100%;
    padding: 0;

    &__image {
      width: 100%;
      height: 27.66rem;
    }

    &__input-button {
      width: 4rem;
      height: 4rem;
      font-size: 2.16rem;
    }
  }
}