@import 'src/assets/scss/variables';

.roadmap {
  position: relative;
  padding: 8.33rem 0 8.33rem;
  //background: #0A0A0A;

  &__title {
    position: relative;
    margin-bottom: 6.5rem;
    text-align: center;
  }

  &__navigation {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: row;
    margin: auto;

    &-button {
      width: 1.66rem;
      display: flex;
      align-items: center;
      cursor: pointer;

      &:first-child {
        margin-right: 1.66rem;
      }

      &--disabled {
        opacity: 0.5;
      }

      & img {
        width: 100%;
      }
    }
  }

  &__slider {
    padding-bottom: 5rem !important;

    & .swiper-wrapper {
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 16.41rem;
        width: 2558px;
        height: 0.33rem;
        background: $greyDefault;
      }

      &:after {
        content: '';
        position: absolute;
        left: 0;
        top: 16.41rem;
        height: 0.33rem;
        width: 0;
        background: $pinkDefault;
        z-index: 1;
      }
    }

    & .swiper-button-prev {
      display: none;
    }

    & .swiper-button-next {
      display: none;
    }

    & .swiper-pagination {
      display: none;
    }
  }

  &__progress-line {
    position: absolute;
    left: 0;
    top: 16.41rem;
    height: 0.33rem;
    background: $pinkDefault;
    z-index: 1;
    transition: width 0.5s ease-in-out;
  }

  &__layout {
    height: 42.5rem;
  }

  & .link-anchor {
    top: -4rem;
  }

}

@media screen and (max-width: 480px) {
  .roadmap {
    padding: 4rem 0 4rem;

    &__title {
      margin-left: 2rem;
      margin-bottom: 3.2rem;
      text-align: left;
    }

    &__navigation {
      display: none;

      &-button {
        width: 4.33rem;
        height: 4.33rem;
        display: flex;
        align-items: center;
        justify-content: center;

        &:first-child {
          margin-right: 0.66rem;
        }

        &--disabled {
          opacity: 0.5;
        }

        & img {
          width: 1.66rem;
          height: 2.66rem;
        }
      }
    }

    &__progress-line {
      display: none;
    }

    &__slider {
      padding-bottom: 0 !important;
    }
  }

  & .link-anchor {
    top: -5rem;
  }
}
