@import "assets/scss/variables";

.roadmap-item {
  position: relative;
  width: 28.16rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 28.16rem;
  margin-right: 2.5rem;
  cursor: pointer;

  &__image {
    width: 12.66rem;
    height: 12.66rem;
    margin-bottom: 5rem;
    transition: filter 0.5s;
    transition-delay: 0.5s;

    &--disabled {
      -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
      filter: grayscale(100%);
    }

    &:hover {
      -webkit-filter: grayscale(0%); /* Safari 6.0 - 9.0 */
      filter: grayscale(0%);
      transition-delay: 0s;
    }
  }

  &__title {
    display: block;
    color: $white;
    font-size: 4.16rem;
    line-height: 3.75rem;
    font-family: 'Bangers';
    text-align: center;
  }

  &__list {
    margin-top: 1rem;
    padding-left: 6.55rem;

    &-item {
      color: $white !important;
    }
  }

  &__point-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 14.58rem;
    width: 4rem;
    height: 4rem;
    background: #000000;
    border-radius: 50%;
    z-index: 2;
  }

  &__point {
    width: 2.5rem;
    height: 2.5rem;

    & path {
      fill: $greyDefault;
      transition: fill;
      transition-delay: 0s;
    }

    &--active {
      & path {
        fill: $pinkDefault;
        transition-delay: 0.5s;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .roadmap-item {
    margin: 0 auto 4rem;

    &__image {
      width: 9rem;
      height: 9rem;
      margin-bottom: 2rem;
      filter: initial;
    }

    &__title {
      font-size: 4rem;
      line-height: 3.75rem;
    }

    &__content {
      width: 100%;
    }

    &__list {
      padding-left: 2rem;

      &-item {
        font-size: 1.33rem !important;
        line-height: 1.59rem !important;
      }
    }

    &__point-wrapper {
      display: none;
    }
  }
}
