@import "assets/scss/variables";

.description {
  padding: 8.33rem 0 8.33rem 0;
  background: $green;

  &__inner {
    & p {
      margin: 0 0 1.3rem 0;
      font-family: 'Bangers';
      font-size: 3.3rem;
      line-height: 3rem;
      text-align: center;

      &:last-child {
        margin: 0;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .description {
    padding: 6.67rem 0 6.67rem 0;

    &__inner {
      & p {
        margin: 0 0 1rem 0;
        font-size: 2.33rem;
        line-height: 2.1rem;
        text-align: left;
      }
    }
  }
}
