@import 'src/assets/scss/variables';

.header-menu {
  position: absolute;
  top: -3.83rem;
  right: -2.5rem;
  width: 0;
  height: 0;
  padding: 10rem 0 3.83rem;
  background: $pinkDefault;
  border-bottom-left-radius: 50%;
  overflow: hidden;
  transition: border-bottom-left-radius 0.5s, width 0.25s, height 0.5s;

  &--open {
    width: 28.75rem;
    height: 45rem;
    border-bottom-left-radius: 0;
  }

  &__close-button {
    position: absolute;
    top: 3.5rem;
    right: 2.75rem;
    width: 2.375rem;
    height: 2.375rem;
    cursor: pointer;
  }

  &__list {
    position: absolute;
    top: 10rem;
    right: 0;
    width: 100%;
  }

  &__item-link {
    display: block;
  }

  &__item {
    width: 100%;
    display: block;
    padding: 0 3.66rem;
    font-size: 2.66rem;
    line-height: 4rem;
    white-space: nowrap;
    font-family: 'Bangers';
    color: $black;
    user-select: none;
    transition: background 0.15s, color 0.15s;

    &:hover {
      color: $white;
      background: rgba(0, 0, 0, 0.1);
      cursor: pointer;
    }
  }

  &__divider {
    display: none;
  }

  &__networks {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  .header-menu {
    position: fixed;
    top: 0;
    right: 0;
    padding: 8.33rem 0 3.33rem;

    &--open {
      width: 100vw;
      height: 50rem;
    }

    &__close-button {
      top: 2.08rem;
      right: 1.75rem;
      width: 2.33rem;
      height: 2.33rem;
    }

    &__list {
      top: 8.33rem;
    }

    &__item {
      padding: 0 1.5rem;
      font-size: 2.66rem;
      line-height: 4rem;
      text-align: right;
    }

    &__divider {
      display: block;
      margin: 1.83rem 0 2.16rem;
      height: 2px;
      background: rgba(245, 245, 245, 0.2);
    }

    &__networks {
      display: flex;
      flex-direction: row;
      justify-content: center;
      height: 2.33rem;

      &-item-link {
        display: block;
        margin-right: 1.66rem;
      }

      &-item {
        height: 2.33rem;
        margin-right: 1.66rem;

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}
