@import "assets/scss/variables";

.home {
  height: 100vh;
  max-height: 80rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('../../assets/images/desktop-home-bg.png');
  background-size: cover;
  background-position: center;

  &__inner {
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__content {
    margin: 0 auto;

    p {
      text-align: center;
      font-family: 'Bangers';
      font-size: 4rem;
      line-height: 3.6rem;
      margin: 2rem 0 2.5rem 0;
      color: $white;

      &:last-child {
        margin: 0;
      }
    }

    &-label {
      display: block;
      text-align: center;
      color: $green;
      font-size: 2.3rem;
      line-height: 2.8rem;
      font-weight: 600;
      font-style: italic;
    }
  }
}

@media screen and (max-width: 480px) {
  .home {
    max-height: 56rem;
    background-image: url('../../assets/images/home-bg-mobile-new.png');

    &__content {
      margin-bottom: 9rem;

      p {
        text-align: left;
        font-size: 2.66rem;
        line-height: 2.4rem;
        margin: 1rem 0 1.2rem 0;
      }

      &-label {
        text-align: left;
        font-size: 1.33rem;
        line-height: 1.59rem;
      }
    }
  }
}
