@import "assets/scss/variables";

.rm-description-item {
  width: calc(50% - 1.25rem);
  margin-bottom: 3.16rem;

  &__inner {
    display: flex;
    flex-direction: row;
  }

  &__image {
    width: 17.91rem;
    min-width: 17.91rem;
    margin-right: 2.5rem;
  }

  &__content {
    flex: 1;

    &-title {
      line-height: 5rem;
      color: $white;
    }

    &-note {
      margin-top: 1rem;
    }

    &-link {
      margin-top: 1.83rem;
      font-family: 'Roboto';
      font-weight: 500;
      font-style: italic;
      font-size: 1.5rem;
      line-height: 1.8rem;
      color: $pinkDefault;
    }
  }
}

@media screen and (max-width: 480px) {
  .rm-description-item {
    width: 100%;
    margin-bottom: 2rem;

    &__inner {
      align-items: center;
    }

    &__image {
      width: 9.33rem;
      min-width: initial;
      height: 9.66rem;
      margin-right: 0.66rem;
    }

    &__content {
      &-title {
        line-height: 2.125rem;
      }

      &-link {
        margin-top: 1rem;
        font-size: 1rem;
        line-height: 1.2rem;
      }
    }
  }
}
