@import 'src/assets/scss/variables';

.button {
  padding: 1rem 4.5rem;
  font-family: 'Bangers';
  font-size: 3.33rem;
  text-align: center;
  // background: $pinkDefault;
  background: #f72a68;
  cursor: pointer;
  user-select: none;
  color: $white;
  transition: background 0.3s, color 0.3s;

  &:hover {
    background: $pinkFocused;
  }

  &:active {
    color: $black;
    background: $green;
  }

  &:disabled {
    background: $greyDefault;
  }
}

@media screen and (max-width: 480px) {
  .button {
    padding: 0.5rem 2.5rem;
    font-size: 1.66rem;
    line-height: 1.66rem;
  }
}
