.opportunities {
  position: relative;
  padding-top: 8.33rem;
  padding-bottom: 8.33rem;

  &__inner {
    max-width: 70.8rem;
    width: 100%;
    margin: 0 auto;
    padding-top: 8.33rem;
  }

  &__content {
    & h3 {
      text-align: center;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3.6rem !important;
    font-size: 1.8rem;
    line-height: 2.2rem;

    & li {
      margin-bottom: 1.75rem;
      text-align: center;

      &:last-child {
        margin-bottom: 0;
      }

      &:nth-child(4) {
        max-width: 59rem;
      }

      &:nth-child(5) {
        max-width: 64rem;
      }
    }
  }

  &__bg1 {
    position: absolute;
    bottom: -23.3rem;
    left: 0;
    width: 33.3rem;
    z-index: -1;
  }

  &__bg2 {
    position: absolute;
    top: -9.16rem;
    right: 0;
    width: 23.5rem;
    z-index: -1;
  }

  & .link-anchor {
    top: -2rem;
  }
}

@media screen and (max-width: 480px) {
  .opportunities {
    padding-top: 0;
    padding-bottom: 0;

    &__list {
      align-items: flex-start;
      margin-top: 2.33rem !important;
      padding-left: 2rem;
      font-size: 1.33rem;
      line-height: 1.59rem;

      & li {
        margin-bottom: 1rem;
        text-align: left;
      }
    }

    &__bg1 {
      display: none;
    }

    &__bg2 {
      display: none;
    }
  }
}
