.team {
  position: relative;

  &__inner {
    padding: 8.33rem 0 7rem 0;
  }

  &__title {
    margin-bottom: 5rem;
    text-align: center;
  }

  &__content {
    display: inline-flex;
    // justify-content: space-between;
    flex-wrap: wrap;
    gap: 0 2.5rem;
  }

  & .link-anchor {
    top: 6rem;
  }
}

@media screen and (max-width: 480px) {
  .team {
    &__inner {
      padding: 2.25rem 0 1rem;
    }

    &__title {
      margin-bottom: 2.33rem;
    }

    &__content {
      width: 100%;
      max-width: 100%;
      flex-direction: row;
      justify-content: space-between;
      // flex-wrap: nowrap;
      grid-gap: initial;
      gap: initial;
      overflow: auto;
    }

    & .link-anchor {
      top: -5rem;
    }
  }
}
