@import "assets/scss/variables";

.community {
  position: relative;
  background: $green;

  &__inner {
    padding: 8.33rem 0;
  }

  &__title {
    color: $pinkDefault;
    text-align: center;
  }

  &__content {
    margin-top: 1.83rem;

    & > p {
      margin: 1.67rem 0 0 0;
      font-family: 'Bangers';
      font-size: 3.33rem;
      line-height: 3rem;
      text-align: center;

      &:first-child {
        margin-top: 0;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .community {
    &__inner {
      padding: 4rem 0 4rem;
    }

    &__content {
      margin-top: 1.33rem;

      & > p {
        margin: 1.15rem 0 0;
        font-size: 1.66rem;
        line-height: 1.5rem;
      }
    }
  }
}
