@import 'src/assets/scss/variables';

.modal {
  width: 66.66rem;
  height: 33.33rem;
  background: $pinkDefault;
  outline: none;

  &Inner {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &Title {
    color: #fff;
  }

  &SubTitle {
    margin-top: 0.66rem;
    font-size: 2.33rem;
    line-height: 2.8rem;
    color: #fff;
    font-weight: 500;
    font-family: 'Roboto';
    font-style: italic;
  }

  &Button {
    width: 17.91rem;
    height: 5.33rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 4rem;
    background: #fff;
    cursor: pointer;

    & img {
      width: 100%;
    }
  }

  &Close {
    position: absolute;
    top: 2rem;
    right: 2rem;
    width: 4.66rem;
    height: 4.66rem;
    cursor: pointer;

    & svg {
      height: 100%;
    }
  }
}

@media screen and (max-width: 480px) {
  .modal {
    width: 100vw;
    height: 20rem;

    &Title {
      font-size: 2.33rem;
      line-height: 2.1rem;
    }

    &SubTitle {
      font-size: 1.33rem;
      line-height: 1.59rem;
    }

    &Button {
      width: 11.66rem;
      height: 3.33rem;
      margin-top: 2rem;

      & img {
        width: initial;
        height: 1.875rem;
      }
    }

    &Close {
      width: 2.33rem;
      height: 2.33rem;
    }
  }
}