.rm-description {
  &__inner {
    padding: 8.33rem 0 0 0;
  }

  &__title {
    text-align: center;
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 6.67rem;
  }
}

@media screen and (max-width: 480px) {
  .rm-description {

    &__inner {
      padding: 0 0 2rem 0;
    }

    &__title {
      font-size: 2.33rem;
      line-height: 2.125rem;
    }

    &__content {
      flex-direction: column;
      margin-top: 3.5rem;
    }
  }
}
