@import "src/assets/scss/variables";

.shop-card {
  width: 38.33rem;
  padding: 2.33rem 2.5rem;
  background: $greyPressed;
  transform: scale(0.95);

  &__image-wrapper {
    position: relative;
    width: 33.33rem;
    height: 33.33rem;
  }

  &__image {
    display: none;
    width: 33.33rem;
    height: 33.33rem;

    &--active {
      display: block;
    }
  }

  &__image-unknown {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16.66rem;
    font-family: "Bangers";
    color: $white;
    background: rgba(0, 0, 0, 0.33);
  }

  &__content {
    margin: 2.33rem 0;

    &-head {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 0.33rem;
    }

    &-title {
      color: $white;
      line-height: 5rem;
    }

    &-price {
      color: $greyDefault;
    }

    &-description {
      margin: 0 0 0.66rem 0;
      font-size: 1.3rem;
      line-height: 1.4rem;
      font-style: italic;
      color: $greyDefault;
    }
  }

  &__submit {
    margin-top: 1.66rem;
  }
}

@media screen and (max-width: 480px) {
  .shop-card {
    // width: 100%;
    padding: 1rem 0.83rem;
    width: 100vw;
    padding: 1rem 0.83rem;
    transform: scale(0.3);
    transform-origin: top left;

    &__image-wrapper {
      width: 100%;
      height: initial;
    }

    &__image {
      width: 100%;
      height: initial;
    }

    &__content {
      height: 12rem;
      margin: 1.66rem 0;

      &-head {
        margin-bottom: 1.33rem;
      }

      &-title {
        font-size: 3.33rem;
        line-height: 3rem;
      }

      &-price {
        font-size: 1.66rem;
        line-height: 2.16rem;
      }

      &-description {
        margin: 0 0 0.75rem 0;
        font-size: 1.25rem;
        line-height: 1.2rem;
        color: $greyDefault;
      }
    }

    &__submit {
      margin-top: 1.33rem;
      font-size: 3.33rem;
      line-height: initial;
    }
  }
}
