.shop {
  padding: 9rem 0 5.16rem;

  &__pc {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &__slider {
    & .swiper-button-prev {
      display: none;
      left: -0.5rem;
    }

    & .swiper-button-next {
      display: none;
      right: -0.5rem;
    }
  }
}

@media screen and (max-width: 480px) {
  .shop {
    padding: 5rem 0 0;

    &__slider {
      height: 18rem;
    }
  }
}
