@import 'src/assets/scss/variables';

.drop-item {
  display: flex;
  flex-direction: row;
  padding-top: 4.5rem;
  margin-top: 13rem;

  &:nth-child(1) {
    margin-top: 5.5rem;
  }

  &--reverted {
    flex-direction: row-reverse;

    & .drop-item__info {
      margin-right: 0;
      margin-left: 13.5rem;
    }
  }

  &__info {
    width: calc(100% - 61.16rem);
    padding: 2.25rem 0 0 0;
    margin-right: 13.5rem;

    &-top {
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      margin-bottom: 2.25rem;
    }

    &-title {
      margin-right: 1.5rem;
      color: $white;
    }

    &-price {
      color: $greyDefault;
      white-space: nowrap;
    }

    &-formula {
      max-width: 100%;
      height: 6.66rem;
      margin-top: 3.58rem;
    }
  }

  &__slider {
    position: relative;
    width: 47.66rem;
    height: 33.33rem;

    &-phrase {
      position: relative;
      z-index: 5;
    }

    & .swiper-container {
      height: 100%;
    }

    & .swiper-slide {
      & img {
        display: block;
        width: 33.33rem;
        margin: 0 auto;
      }
    }

    & .swiper-button-prev {
      position: absolute;
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      background: $black;
      transition: background 0.25s;
      z-index: 10;

      &::after {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        content: '';
        display: block;
        width: 1.67rem;
        height: 2.66rem;
        margin: auto;
        background-image: url('/assets/images/sliderPrevButton.svg');
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-position: center;
      }

      &:hover {
        background: #151515;
      }
    }

    & .swiper-button-next {
      position: absolute;
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      background: $black;
      transition: background 0.25s;
      z-index: 10;

      &::after {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        content: '';
        display: block;
        width: 1.67rem;
        height: 2.66rem;
        margin: auto;
        background-image: url('/assets/images/sliderNextButton.svg');
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-position: center;
      }

      &:hover {
        background: #151515;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .drop-item {
    flex-direction: column-reverse;
    padding-top: 3.5rem;
    margin-top: 5.35rem;

    &:nth-child(1) {
      margin-top: 3rem;
    }

    &:nth-child(2) {
      margin-top: 5.35rem;
    }

    &--reverted {
      & .drop-item__info {
        margin-right: 0;
        margin-left: 0;
      }
    }

    &__info {
      width: 100%;
      margin-right: 0;
      padding: 0 0 0 0;

      &-top {
        margin-bottom: 1.67rem;
      }

      &-title {
        font-size: 2.33rem;
        line-height: 2.1rem;
      }

      &-price {
        font-size: 1.67rem;
        line-height: 2rem;
      }

      &-formula {
        max-width: 100%;
        height: 3.6rem;
        margin-top: 1.33rem;
      }
    }

    &__slider {
      // width: 29.33rem;
      width: 100%;
      max-width: 100%;
      height: 31.33rem;

      & .swiper-slide {
        & img {
          display: block;
          // width: 19.33rem;
          width: 100%;
          margin: 0 auto;
        }
      }

      & .swiper-button-prev {
        display: none;
        left: -0.5rem;
      }

      & .swiper-button-next {
        display: none;
        right: -0.5rem;
      }
    }
  }
}
