@import "assets/scss/variables";

.label-say {
  position: absolute;
  top: -2.83rem;
  right: 0;
  width: 24rem;
  padding: 1rem 1.67rem;
  color: $black;
  font-family: 'Bangers';
  font-size: 2.33rem;
  line-height: 2.66rem;
  background: $white;
  z-index: 10;

  &__icon {
    position: absolute;
    top: 99%;
    right: 3.5rem;
    width: 2.33rem;
    height: 2.33rem;
  }
}

@media screen and (max-width: 480px) {
  .label-say {
    top: -3.75rem;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: fit-content;
    font-size: 1.41rem;
    line-height: 1.27rem;

    &__icon {
      width: 1.33rem;
      height: 1.33rem;
    }
  }
}