@import "assets/scss/variables";

.heroes-slider {
  padding: 4rem 0 4rem 0;

  &__item {
    height: 100%;
  }

  & .swiper-slide {
    height: 32rem !important;
    cursor: pointer;

    & img {
      height: 100%;
      display: block;
      margin: 0 auto;
    }
  }

  & .swiper-button-next {
    display: none !important;
  }

  & .swiper-button-prev {
    display: none !important;
  }
}

@media screen and (max-width: 480px) {
  .heroes-slider {
    padding: 0 1rem 4rem 1rem;

    & .swiper-slide {
      & img {
        height: initial;
        width: 100%;
      }
    }
  }
}