@import "assets/scss/variables";

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

* {
  box-sizing: border-box;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
}

html {
  font-size: 12px;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  max-width: 100vw;
  min-height: 100vh;
  font-weight: 400;
  background: $black;
  overflow-x: hidden;
}

.container {
  width: 133.33rem;
  max-width: 100%;
  min-width: auto;
  margin: 0 auto;
  padding: 0 6.67rem;
}

.content {
  & p {
    margin: 0;
    font-size: 1.83rem;
    line-height: 2.19rem;
    font-family: 'Roboto';
    font-style: italic;
    font-weight: 500;
    color: $white;
  }

  & p.content__note {
    margin: 1.33rem 0 0 0;
    font-size: 1.5rem;
    line-height: 1.8rem;
    color: $greyDefault;
    font-family: 'Roboto';
    font-style: italic;
    font-weight: 500;
  }

  & ul {
    margin: 0;
    color: $white;
    font-family: 'Roboto';
    font-style: italic;
    font-weight: 500;
  }
}

h2 {
  margin: 0;
  color: $green;
  font-size: 5rem;
  line-height: 5rem;
  font-family: 'Bangers';
  font-weight: normal;
}

h3 {
  margin: 0;
  color: $green;
  font-size: 3.33rem;
  line-height: 3.33rem;
  font-family: 'Bangers';
  font-weight: normal;
}

h4 {
  margin: 0;
  color: $green;
  font-size: 2.33rem;
  line-height: 3.025rem;
  font-family: 'Bangers';
  font-weight: normal;
}

.link-anchor {
  position: absolute;
  top: -10rem;
}

.hide {
  display: none !important;
}

@media screen and (max-width: 1920px) {
  html {
    font-size: 12px;
  }
}

@media screen and (max-width: 1680px) {
  html {
    font-size: 10.5px;
  }
}

@media screen and (max-width: 1600px) {
  html {
    font-size: 10px;
  }
}

@media screen and (max-width: 1440px) {
  html {
    font-size: 10.5px;
  }
}

@media screen and (max-width: 1400px) {
  html {
    font-size: 8.75px;
  }
}

@media screen and (max-width: 1366px) {
  html {
    font-size: 8.5px;
  }
}

@media screen and (max-width: 1280px) {
  html {
    font-size: 8px;
  }
}

@media screen and (max-width: 1024px) {
  html {
    font-size: 10px;
  }
}

@media screen and (max-width: 768px) {
  html {
    font-size: 7.5px;
  }
}

@media screen and (max-width: 600px) {
  html {
    font-size: 8px;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 13.44px;
  }

  h2 {
    font-size: 4rem;
    line-height: 3.6rem;
  }

  h3 {
    font-size: 2.33rem;
    line-height: 2.1rem;
  }

  h4 {
    font-size: 2.33rem;
    line-height: 3.025rem;
  }

  .container {
    padding: 0 1rem;
  }

  .content {
    & p {
      font-size: 1.33rem;
      line-height: 1.59rem;
    }

    & p.content__note {
      margin: 1rem 0 0 0;
      font-size: 1rem;
      line-height: 1.2rem;
    }
  }

  .root {
    position: relative;
    max-width: 375px;
    overflow: scroll;
  }

  .link-anchor {
    position: absolute;
    top: -5rem;
  }
}

@media screen and (max-width: 375px) {
  html {
    font-size: 12px;
  }
}

@media screen and (max-width: 320px) {
  html {
    font-size: 10.24px;
  }
}
