.clan {
  &__inner {
    display: flex;
    flex-direction: row;
    padding-top: 8.33rem;
  }

  &__grid {
    width: 48.5rem;
    //height: 48.5rem;
    height: fit-content;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 0.75rem 0.75rem;
    grid-template-areas:
    ". . ."
    ". . ."
    ". . .";

    & > img {
      display: block;
      width: 100%;
      max-height: 100%;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
    margin-left: 12.6rem;

    &-title {
      margin-bottom: 2.6rem;
    }
  }

  &__title-mobile {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  .clan {
    &__inner {
      display: flex;
      flex-direction: column;
      padding-top: 5.33rem;
    }

    &__grid {
      width: 100%;
      height: auto;
      margin-top: 3rem;
    }

    &__content {
      margin: 2.33rem 0 0 0;

      &-title {
        display: none;
      }
    }

    &__title-mobile {
      display: block;
      text-align: center;
    }
  }
}
