@import "assets/scss/variables";

.team-item {
  display: flex;
  flex-direction: row;
  width: 38.33rem;
  max-width: 38.33rem;
  margin-bottom: 3.33rem;

  &__avatar {
    width: 17.91rem;
    min-width: 17.91rem;
    height: 17.91rem;
    margin-right: 2.5rem;
    background: white;
  }

  &__title {
    color: $white;
    line-height: 3rem;
  }

  &__note {
    color: $white !important;
    margin-top: 1rem !important;
    font-size: 1.83rem !important;
    line-height: 2.19rem !important;
  }

  &__socials {
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.33rem;

    &-item {
      margin-right: 1.5rem;

      &:last-child {
        margin-right: 0;
      }

      & svg {
        height: 2rem;
        max-width: 2.5rem;

        & path {
          fill: $white;
        }
      }

      &:hover {
        & svg {
          & path {
            fill: $pinkDefault;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .team-item {
    width: 8rem;
    flex-direction: column;
    margin-right: 0;
    margin-bottom: 1.5rem;

    &__avatar {
      width: 8rem;
      min-width: initial;
      height: 8rem;
      margin-right: 0;
    }

    &__member {
      font-size: 1.5rem;
      line-height: 1.5rem;
    }

    &__content {
      margin-top: 1.33rem;
    }

    &__note {
      margin-top: 0.5rem !important;
      font-size: 1rem !important;
      line-height: 1.2rem !important;
    }

    &__socials {
      margin-top: 0.5rem;
  
      &-item {
        margin-right: 0.5rem;
  
        &:last-child {
          margin-right: 0;
        }
  
        & svg {
          height: 1.5rem;
          max-width: 2rem;
  
          & path {
            fill: $white;
          }
        }
      }
    }
  }
}
