@import "assets/scss/variables";

.header {
  position: fixed;
  top: 0;
  left: 0;
  height: 10rem;
  width: 100%;
  background: linear-gradient(180deg, #030303 0%, rgba(3, 3, 3, 0) 100%);
  z-index: 1000;

  &__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #030303 0%, rgba(3, 3, 3, 0.8) 100%);
    opacity: 0;
    z-index: -1;
    transition: opacity 0.2s;

    &--dark {
      opacity: 1;
    }
  }

  &__inner {
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__logo {
    height: 3.5rem;
    cursor: pointer;
  }

  &__logo-mobile {
    display: none;
    cursor: pointer;
  }

  &__counter {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    width: fit-content;
    color: $green;
    font-family: 'Bangers';
    font-size: 3.3rem;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    -webkit-text-stroke: 1px black;
  }

  &__gap {
    flex: 1;
  }

  &__gap-mobile {
    display: none;
  }

  &__networks {
    display: flex;
    flex-direction: row;
    margin-left: 4.5rem;

    &-button {
      display: block;
      height: 2.33rem;
      margin-right: 28px;
      cursor: pointer;

      &:last-child {
        margin-right: initial;
      }

      & > svg {
        height: 100%;
      }

      &:hover {
        & > svg path {
          fill: $pinkDefault;
        }
      }

      &:active {
        & > svg path {
          fill: $pinkFocused;
        }
      }
    }
  }

  &__menu-button {
    position: relative;
    width: 3rem;
    margin-left: 7.33rem;
    cursor: pointer;

    & > svg {
      height: 100%;
    }

    &:hover {
      & > svg path {
        fill: $pinkDefault !important;
      }
    }
  }

  &__giveaway {
    font-size: 2.5rem;
    color: $green;
    -webkit-text-stroke: initial;
    transform: scale(1);
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }
}

@media screen and (max-width: 480px) {
  .header {
    height: 5rem;
    background: linear-gradient(180deg, #030303 0%, rgba(3, 3, 3, 0.8) 100%);

    &__logo {
      display: none;
    }

    &__logo-mobile {
      display: block;
      height: 2.5rem;
      width: 4.33rem;
      min-width: 4.33rem;
    }

    &__counter {
      font-size: 2rem;
    }

    &__gap-mobile {
      display: flex;
      flex: 1;
    }

    &__networks {
      display: none;
    }

    &__menu-button {
      margin-left: 0;

      & > svg {
        width: 2.33rem;
        height: 1.66rem;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .header {
    &__background {
      display: none;
    }

    &__giveaway {
      font-size: 1.15rem;
      color: $green;
      transform: scale(1);
      transition: transform 0.3s;
  
      &:hover {
        transform: scale(1.1);
      }
    }
  }
}
