@import "assets/scss/variables";

.second-drop {
  position: relative;

  &__inner {
    padding: 8.33rem 0 8.33rem 0;
  }

  &__head {
    margin: 0 auto;
    width: 100%;
    max-width: 79.16rem;

    & p {
      text-align: center;
    }
  }

  &__title {
    text-align: center;
  }

  &__sub-title {
    display: block;
    margin: 1.5rem 0 3.33rem 0;
    text-align: center;
    font-weight: 500;
    font-family: 'Roboto';
    font-size: 2.33rem;
    line-height: 2.8rem;
    font-style: italic;
    color: $green;
  }

  &__content {
    display: flex;
    flex-direction: row;
    margin-top: 5rem;
  }

  &__grid {
    width: 53.33rem;
    //height: 53.33rem;
    height: fit-content;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 1.67rem;
    grid-row-gap: 1.67rem;

    & > img {
      display: block;
      width: 100%;
      max-height: 100%;
    }
  }

  &__grid-mobile {
    display: none;
  }

  &__info {
    flex: 1;
    margin-left: 7.91rem;

    & h3 {
      margin-top: 3.33rem;
      color: $white;
    }

    & p {
      margin-top: 1.67rem !important;
    }

    &-divider {
      margin-top: 2.83rem;
      border-top: 2px solid rgba(255, 255, 255, 0.2);
    }
  }

  & .link-anchor {
    top: -4rem;
  }
}

@media screen and (max-width: 480px) {
  .second-drop {
    &__inner {
      padding: 7rem 0 2.66rem 0;
    }

    &__sub-title {
      margin: 1rem 0 2.66rem 0;
      font-size: 1.67rem;
      line-height: 2rem;
    }

    &__content {
      flex-direction: column;
      margin-top: 0;
    }

    &__grid {
      display: none;
      width: 100%;
    }

    &__grid-mobile {
      width: 100%;
      height: auto;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(1, 1fr);
      grid-column-gap: 0.66rem;
      grid-row-gap: 0.66rem;
      margin-top: 2.66rem;

      & > img {
        display: block;
        width: 100%;
        max-height: 100%;
      }
    }

    &__info {
      margin-left: 0;

      & h3 {
        margin-top: 2.66rem;
      }

      & p {
        margin-top: 1rem !important;
      }

      &-divider {
        margin-top: 1.66rem;
      }
    }

    & .link-anchor {
      top: 0;
    }
  }
}
