.giveaway {
  position: relative;
  height: 94rem;
  padding: 6rem 0 0;
  background-size: cover;
  background-position: center;
  background-image: url("../../assets/images/total-giveaway-bg.png");

  &__inner {
    height: 100%;
    width: 100%;
  }

  &__content {
    height: 100%;
    width: calc(100% - 16rem);
    margin: 0 auto;
    background-position: top center;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("../../assets/images/total-giveaway-content.png");
  }

  &__mobile {
    display: none;
  }

  & .link-anchor {
    top: -10rem;
  }
}

@media screen and (max-width: 480px) {
  .giveaway {
    height: initial;
    padding: 0;
    background-image: initial;

    &__inner {
    }
  
    &__content {
      background-image: initial;
    }

    &__mobile {
      width: 100%;
      display: block;
    }

    & .link-anchor {
      top: -4rem;
    }
  }
}
