@import 'src/assets/scss/variables';

.faq-item {
  position: relative;
  padding-right: 4.58rem;

  &__title {
    position: relative;
    color: $white !important;
    cursor: pointer;
  }

  &__expand {
    position: absolute;
    width: 2.66rem;
    height: 3.33rem;
    right: 0;
    top: 0;
    padding: 0.83rem 0;
    transform: rotateZ(0deg);
    transition: transform 0.3s;
    cursor: pointer;

    &--expanded {
      transform: rotateZ(180deg);
    }
  }

  &__content {
    max-height: 0;
    transition: max-height 0.3s;
    overflow: hidden;

    &--expanded {
      max-height: 40rem;
    }

    & p {
      color: $greyDefault !important;
    }
  }

  &__gap {
    height: 1.67rem;
  }
}

@media screen and (max-width: 480px) {
  .faq-item {
    &__title {
      font-size: 1.8rem;
      line-height: 2.1rem;
    }

    &__content {
      & p {
        font-size: 1.33rem;
        line-height: 1.59rem;
      }
    }

    &__gap {
      height: 1rem;
    }
  }
}
