@import "assets/scss/variables";

.first-drop {
  position: relative;

  &__inner {
    padding-top: 8.33rem;
    padding-bottom: 8.33rem;
  }

  &__title {
    text-align: center;
  }

  &__sub-title {
    display: block;
    margin-top: 1.5rem;
    text-align: center;
    font-weight: 500;
    font-family: 'Roboto';
    font-size: 2.33rem;
    line-height: 2.8rem;
    font-style: italic;
    color: $green;
  }

  & .link-anchor {
    top: -4rem;
  }
}

@media screen and (max-width: 480px) {
  .first-drop {

    &__inner {
      padding-top: 7rem;
      padding-bottom: 0;
    }

    &__sub-title {
      margin-top: 1rem;
      font-size: 1.67rem;
      line-height: 2rem;
    }

    & .link-anchor {
      top: 0;
    }
  }
}
