.faq {
  position: relative;

  &__inner {
    padding: 8.33rem 0 26.25rem 0;
  }

  &__title {
    text-align: center;
  }

  &__content {
    margin-top: 4.16rem;

    &-divider {
      margin: 2.5rem 0;
      border-top: 2px solid rgba(255, 255, 255, 0.2);
    }
  }

  &__logo {
    width: 18.33rem;
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    right: 0;
    margin: 0 auto;
  }

  &__bg {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: -1;
  }
}

@media screen and (max-width: 480px) {
  .faq {
    &__inner {
      padding: 5.66rem 0 23rem 0;
    }

    &__content {
      margin-top: 4.16rem;

      &-divider {
        margin: 2.33rem 0;
      }
    }

    &__logo {
      width: 9.33rem;
    }
  }
}
